.publish {
  min-height: 100vh;
  background-color: white;
}
.publish .foot {
  position: fixed;
  height: 60Px;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  width: 100%;
}
.publish .foot > div {
  margin: 0;
}
.publish .foot .preview {
  width: 112Px;
  background-color: white;
  border: 1Px solid #68e3ce;
  color: #68e3ce;
}
.publish .foot .pb {
  width: 202Px;
}
