.new-patient {
  background-color: white;
}
.new-patient .item {
  display: -webkit-flex;
  display: flex;
  padding: 0.4rem;
  -webkit-align-items: center;
          align-items: center;
  border-top: 1Px solid #eee;
}
.new-patient .item .title {
  padding: 4px 0;
  color: #000;
  font-size: 14px;
}
.new-patient .item .time {
  font-size: 12px;
  color: #999999;
}
.new-patient .item .button {
  min-width: 56px;
  -webkit-align-content: flex-end;
          align-content: flex-end;
  text-align: center;
  height: 30Px;
  line-height: 30Px;
  width: 80Px;
  background-color: #68e3ce;
  padding: 0 8Px;
  color: white;
  font-size: 14px;
  border-radius: 3Px;
}
.new-patient .item .accpt {
  width: 80Px;
  padding: 0 8Px;
  text-align: center;
  font-size: 16Px;
}
