* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
* {
  -webkit-tap-highlight-color: transparent;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", SimSun, sans-serif;
}
.App {
  max-width: 450Px;
  margin: 0 auto;
  overflow: hidden;
  background: transparent;
}
a:hover {
  text-decoration: none;
}
html {
  background: transparent !important;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.am-navbar {
  position: fixed;
  max-width: 450Px;
  width: 100%;
  z-index: 998;
}
a:active {
  text-underline: none !important;
}
a:link {
  text-underline: none !important;
}
.button {
  margin: auto;
  width: 284PX;
  height: 44PX;
  line-height: 44PX;
  color: white;
  text-align: center;
  font-size: 16px;
  border-radius: 22PX;
  background-color: #68e3ce;
}
