.published {
  width: 100%;
  background-color: white;
}
.published .hr {
  height: 1Px;
  background-color: #f2f2f2;
}
.published .day {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  text-align: center;
  font-size: 20Px;
}
.published .day span {
  display: inline-block;
  padding-top: 20px;
}
.published .day span.date {
  font-size: 30Px;
}
.published .day span.month {
  font-size: 16Px;
}
.published .day .time {
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 25%;
}
.published .day .articles {
  width: 75%;
  padding: 15Px 0;
  text-align: left;
}
.published .day .articles .article {
  display: -webkit-flex;
  display: flex;
  padding: 15Px 0;
}
.published .day .articles .article .img {
  width: 95Px;
  height: 95Px;
  overflow: hidden;
}
.published .day .articles .article .img img {
  width: 100%;
}
.published .day .articles .article .content {
  width: 100%;
  margin-left: 10px;
  font-size: 14Px;
}
