* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
.order-doc .am-card,
.order-doc .am-navbar {
  background: #68e3ce;
}
.order-doc .header-img {
  width: 1.33333rem;
  height: 1.33333rem;
  border-radius: 50%;
}
.order-doc .title {
  color: white;
  font-size: 14px;
}
.order-doc .title > span {
  margin-left: 8px;
}
.order-doc .title .name {
  font-weight: bold;
}
.order-doc .hospital {
  color: white;
  margin-top: 5px;
}
.order-doc .hospital > span {
  margin-left: 10px;
  font-size: 14px;
}
.order-doc footer > p {
  margin-top: 5px;
  font-size: 14px;
  color: white;
  margin-left: 8px;
}
.order-doc .am-accordion-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.order-doc .am-accordion-header {
  color: #999999 !important;
}
.order-doc .arrow {
  right: 140Px !important;
}
.order-doc .code {
  color: #FF9900;
}
.order-doc .img-pick {
  padding: 10px;
}
.order-doc .img-pick > p {
  color: #000;
  font-size: 17px;
  margin-left: 0;
  margin-right: 5px;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  padding: 4px 4px;
}
.order-doc .img-pick > p span {
  color: #666666;
  font-size: 12px;
}
.order-doc .phone {
  padding: 0.53333rem 0;
  text-align: center;
  color: #999999;
  font-size: 12px;
}
.order-doc .footer {
  z-index: 999;
  background: white;
  width: 100%;
  max-width: 450Px;
  text-align: right;
  height: 1.33333rem;
  position: fixed;
  bottom: 0;
}
.order-doc .footer span {
  display: inline-block;
  background: #68e3ce;
  font-size: 16px;
  color: white;
  width: 4.13333rem;
  height: 1.33333rem;
  line-height: 3;
  text-align: center;
}
