.countBox {
  background-color: white;
  height: 100vh;
  font-size: 14px;
}
.countBox .countTop {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  position: relative;
  margin-top: 13px;
  line-height: 18px;
}
.countBox .countTop > img {
  width: 0.42667rem;
  height: 0.48rem;
  position: absolute;
  right: 21px;
}
.countBox .showDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 0.4rem;
}
