.tips .select-money {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 10px 0;
}
.tips .select-money > span {
  display: block;
  border: 1Px solid #b3b3b3;
  text-align: center;
  width: 20%;
  margin: 1% 2.2%;
  height: 32Px;
  border-radius: 16Px;
  line-height: 32Px;
  font-size: 14Px;
}
.tips .select-money > span.active {
  border-color: #68e3ce !important;
  color: #68e3ce !important;
}
.tips .img {
  margin-right: 10px;
}
.tips .button {
  width: 80%;
  height: 44Px;
  line-height: 44Px;
  text-align: center;
  margin: 0 auto;
  background-color: #68e3ce;
  color: white;
  border-radius: 22Px;
  font-size: 16Px;
}
