* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
.doctors {
  position: relative;
}
.doctors .arrow {
  z-index: 9999;
  top: 0.8rem !important;
}
.doctors .am-badge {
  width: 43px;
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -999;
  margin: auto;
}
.doctors .am-badge .am-badge-corner {
  font-size: 12px;
  background: #FF9900;
  z-index: 9;
}
.doctors .am-accordion-header {
  background: transparent !important;
  z-index: 1 !important;
  height: auto !important;
}
.doctors .am-accordion-item {
  background-color: #ffffff;
}
.doctors .panel:before {
  width: 2.08rem;
  height: 2.08rem;
  position: absolute;
  right: 0;
  content: attr(data-content);
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 0.8rem;
  background-position: right top;
}
.doctors .content {
  width: 95%;
  padding: 10px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: center;
          align-content: center;
}
.doctors .content .am-badge {
  position: relative;
  width: auto;
  height: auto;
  top: 0;
  right: 0;
  z-index: auto;
  margin: 0;
}
.doctors .content .am-badge .header-img {
  height: 50px;
  border-radius: 50%;
}
.doctors .content .doc-brief {
  width: 70%;
  font-size: 10px;
  color: #666666;
}
.doctors .content .doc-brief > span {
  line-height: 0;
  margin-right: 6px;
}
.doctors .content .doc-brief .name {
  font-size: 16px;
  color: #000;
}
.doctors .content .doc-brief p {
  max-width: 250px;
  line-height: 1;
  font-size: 12px;
  color: #999999;
}
.doctors .btn-group {
  padding: 9px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.doctors .btn-group a {
  width: 2.74667rem;
  height: 34Px;
  font-size: 15px;
  line-height: 34Px;
  background-color: #68e3ce;
  color: white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.19);
}
.doctors .btn-group a:last-child {
  background: #f8f8f8;
  color: #999999;
}
