* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
.nav-foot-patient .phone {
  z-index: -1;
  width: 100%;
  position: fixed;
  max-width: 450Px;
  bottom: 2.13333rem;
  margin-top: 0;
  padding: 10px;
  font-size: 16px;
  text-align: center;
  color: #999999;
}
.nav-foot-patient .nav-foot {
  z-index: 999;
  position: fixed;
  width: 100%;
  max-width: 450Px;
  bottom: 0;
  height: 2.13333rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  background: url(../../static/media/blue@3x.cd695a1b.png);
  background-size: 10rem 100%;
  background-position-x: center;
}
.nav-foot-patient .nav-foot span {
  height: 1.33333rem;
  color: #333;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 33%;
  display: -webkit-flex;
  display: flex;
  font-size: 12px;
  text-underline: none;
  text-decoration: none;
  padding-bottom: 4px;
  text-align: center;
}
.nav-foot-patient .nav-foot span:active,
.nav-foot-patient .nav-foot span.active {
  font-weight: bold;
  color: hsl(170, 69%, 65%);
}
.nav-foot-patient .nav-foot span:focus {
  outline: none;
}
.nav-foot-patient .nav-foot span img {
  padding-bottom: 4px;
  height: 0.53333rem;
}
.nav-foot-patient .white-space {
  height: 3.2rem;
}
.nav-foot-doc {
  z-index: 999;
  width: 100%;
  max-width: 450Px;
  position: fixed;
  bottom: 0;
}
