.record-list .recordList {
  padding: 9px 0 0 0;
  margin-bottom: 42px;
}
.record-list .recordList .recordP {
  width: 96Px;
  height: 44Px;
  background: #fe5252;
  color: #fff;
  font-size: 15Px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.record-list .recordList .recordDiv {
  margin: 8px 0.53333rem 0;
  padding-bottom: 9px;
  font-size: 14px;
  border-bottom: 2px #eee solid;
}
.record-list .recordList .recordDiv > div:nth-child(1) {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.record-list .recordList .recordDiv > div:nth-child(1) > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.record-list .recordList .recordDiv > div:nth-child(1) > span {
  font-size: 10px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  margin-top: 6px;
  color: #999;
}
.record-list .recordList .recordDiv .appointAvatar {
  width: 50Px;
  height: 50Px;
  border-radius: 50%;
  margin-right: 12px;
}
.record-list .recordList .recordDiv .appointInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.record-list .recordList .recordDiv .appointInfo > div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: #666;
}
.record-list .recordList .recordDiv .appointInfo > div .appointName {
  font-size: 16px;
  font-weight: bold;
  color: #000;
}
.record-list .recordList .recordDiv .appointInfo > div .appointSpan {
  margin: 0 5px;
}
.record-list .recordList .recordDiv .appointInfo > div .appointGrade {
  -webkit-flex: 1 1;
          flex: 1 1;
  border-radius: 1Px;
  border: 1px solid #ff9900;
  color: #FF9900;
  text-align: center;
  margin-right: 5px;
  font-size: 11px;
  padding: 1px;
}
.record-list .recordList .recordDiv .recordBottomDiv {
  margin-top: 8px;
}
.record-list .recordList .recordDiv .recordBottomDiv > p {
  color: #000;
  font-size: 15px;
  font-weight: bold;
  display: inline-block;
}
.record-list .recordList .recordDiv .recordBottomDiv > span {
  line-height: 1.5;
  color: #666;
}
