.patient-remark .pain-detail {
  color: #999999;
  font-size: 15Px;
}
.patient-remark .pic {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.patient-remark .pic > img {
  width: 45%;
  height: 45%;
  margin: 2.5% 0 0 3.3%;
  background-color: #999999;
}
.patient-remark .tag {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.patient-remark .tag .pain {
  display: block;
  margin: 4px;
  font-size: 12px;
  padding: 4px 9px;
  border: 1Px solid #999999;
  border-radius: 5Px;
}
.patient-remark .tag .active {
  border-color: #68e3ce;
  color: #68e3ce;
}
.patient-remark .button {
  margin: 0;
  height: 30Px;
  line-height: 30Px;
  width: 60Px;
  font-size: 14PX;
}
