.doc-list {
  position: relative;
}
.doc-list .am-badge-corner {
  width: 80px;
  padding: 8px;
  position: absolute;
  left: -32px;
  top: 8px;
  background-color: #FF9900;
  color: #fff;
  white-space: nowrap;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  text-align: center;
  font-size: 8px;
}
.doc-list .doc {
  padding: 10px 0;
  max-width: 400px;
  margin-left: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.doc-list .doc .header-img {
  display: inline-block;
  background: #68e3ce;
  margin-top: 4px;
  margin-right: 4Px;
  width: 1.33333rem;
  height: 1.33333rem;
  border-radius: 50%;
}
.doc-list .doc .brief {
  width: 5.6rem;
  overflow: hidden;
}
.doc-list .doc .brief span {
  margin-right: 10px;
}
.doc-list .doc .brief header .name {
  color: #333333;
  font-size: 18px;
  font-weight: bold;
}
.doc-list .doc .brief header .subject,
.doc-list .doc .brief header .level {
  color: #999999;
  font-size: 14px;
}
.doc-list .doc .brief section .hospital {
  font-size: 16px;
  color: #333333;
}
.doc-list .doc .brief footer .master {
  width: 5.6rem;
  font-size: 15px;
  color: #999999;
}
