.doctor-wallet .background {
  background-color: #68e3ce;
}
.doctor-wallet .background div {
  color: white;
  background-color: transparent;
}
.doctor-wallet .background .big {
  font-size: 26Px;
}
.doctor-wallet .background .small {
  font-size: 14px;
  font-weight: lighter;
}
.doctor-wallet .background .am-result-button {
  margin-top: 30px;
  text-align: center;
}
.doctor-wallet .background .am-button {
  margin: 0 auto;
  background-color: transparent;
  border: 1Px solid white;
  line-height: 44Px;
  color: white;
  border-radius: 22Px;
  width: 180Px;
  height: 44Px;
}
.doctor-wallet .total {
  font-size: 16px;
  color: #fe5252;
}
.doctor-wallet .income {
  color: #FF9900;
  font-size: 16px;
}
.doctor-wallet .am-accordion .am-accordion-item .am-accordion-header {
  padding-left: 0;
  padding-right: 30px;
}
