* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
.doctor-index {
  width: 10rem;
}
.doctor-index .am-result {
  max-width: 450Px;
  padding: 50Px 0 20Px;
}
.doctor-index .avator {
  width: 60Px;
  height: 60Px;
  border-radius: 50%;
}
.doctor-index .name {
  max-width: 450Px;
  text-align: center;
  font-size: 16px;
  margin-top: 30px;
}
.doctor-index .name .sex {
  height: 0.34667rem;
}
.doctor-index .am-list {
  max-width: 450Px;
}
.doctor-index .am-search {
  height: 36Px;
  background-color: transparent;
}
.doctor-index .am-search .am-search-input {
  border: 1Px solid #b3b3b3;
  border-radius: 18Px;
}
.doctor-index .am-list-item {
  max-width: 400px;
  border-bottom: 1Px solid #ebebeb !important;
}
.doctor-index .patient-avator {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
}
.doctor-index p.patient-message {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.doctor-index p.patient-message .time {
  font-size: 10px;
  color: #999999;
}
.pain .list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.pain .list .item {
  margin: 10px 10px 5px 0;
  padding: 0 5Px;
  height: 28Px;
  line-height: 28Px;
  border-radius: 14Px;
  color: #999999;
  border: 1px solid #999999;
}
.pain .list .item.active {
  color: #68e3ce;
  border-color: #68e3ce;
}
.pain .footer {
  display: -webkit-flex;
  display: flex;
  bottom: 0;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.pain .footer .button {
  width: 110Px;
  height: 38Px;
  line-height: 38Px;
  border-radius: 19Px;
  font-size: 16px;
  background: #fff;
  border: 1px solid #68e3ce;
  color: #68e3ce;
}
.pain .footer .button:last-child {
  border-color: #68e3ce;
  background-color: #68e3ce;
  color: white;
}
