* {
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
}
.qrcode-react {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  background-color: #ffffff;
  text-align: center;
}
.qrcode-react p {
  font-size: 20px;
  padding-top: 51.8%;
}
.qrcode-react svg {
  margin-top: 10%;
}
.message * {
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
}
.message .chat {
  padding: 10px 0;
  overflow: hidden;
  text-align: center;
}
.message .chat .tips-alert {
  padding: 2px 0.32rem;
  font-size: 12Px;
  line-height: 20Px;
  height: 20Px;
  text-align: center;
  -webkit-flex: 0 1;
          flex: 0 1;
  border-radius: 10Px;
  background-color: #FE5252;
}
.message .chat > div {
  line-height: 1.5;
  font-size: 16px;
  color: white;
}
.message .chat .message-data {
  text-align: left;
  background-color: #68e3ce;
  color: white;
  padding: 8Px;
  max-width: 70%;
  border-radius: 6Px;
  word-break: break-all;
}
.message .chat .message-img {
  margin-top: 10px;
  width: 70%;
  height: 70%;
  top: 0;
  color: #68e3ce;
}
.message .chat .message-img.big-img {
  position: fixed;
  top: 10px;
  z-index: 999;
  width: 100%;
  height: auto;
}
.message .chat .big-img-back {
  position: fixed;
  background: #000000;
  z-index: 998;
  width: 100vw;
  height: 100vh;
  top: 0;
}
.message .chat .avatar {
  height: 1.06667rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.message .chat .avatar img {
  height: 0.74667rem;
  margin: 1px 10px;
}
.message .chat .avatar .name {
  color: #152b23;
  font-size: 10px;
}
.message .chat .to {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.message .chat .to .avatar {
  -webkit-order: 1;
          order: 1;
}
.message .chat .to > span:after {
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  right: -2px;
  top: 0.37333rem;
  content: '';
  border: 100px;
  background-color: #68e3ce;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  z-index: -1;
}
.message .chat .from {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.message .chat .from > span:after {
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  left: -2px;
  top: 0.37333rem;
  content: '';
  border: 100px;
  background-color: #68e3ce;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  z-index: -1;
}
.message .bottom-input {
  position: fixed;
  width: 100%;
  max-width: 450Px;
  box-sizing: border-box;
  bottom: 0;
}
.message .bottom-input .speak-input-wrap {
  background-color: white;
}
.message .bottom-input .speak-input-wrap .voice {
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.message .bottom-input .speak-input-wrap .voice > img {
  margin-right: 5px;
  height: 32Px;
}
.message .bottom-input .speak-input-wrap .voice .speak {
  font-size: 15Px;
  margin: 6Px 10px;
  width: 70%;
  height: 32Px;
  line-height: 32Px;
  text-align: center;
  border: 1Px solid #999999;
  border-radius: 4Px;
}
.message .bottom-input .alert {
  border-top: 1Px solid #f2f2f2;
  height: auto;
}
.message .bottom-input .alert > img {
  height: 50Px;
  margin: 20Px 10px;
}
.message .bottom-input .tip {
  position: absolute;
  width: 1.33333rem;
  height: 1.33333rem;
  right: 20px;
  top: -1.86667rem;
}
.message .bottom-input .tip:before {
  content: '';
  position: absolute;
  right: 8px;
  top: 2px;
  background: red;
  width: 8px;
  height: 8px;
  border-radius: 8px;
}
.message .bottom-input .tip > img {
  height: 1.33333rem;
}
.message .send-message {
  font-size: 16px;
  width: 60Px;
  line-height: 30Px;
  height: 30Px;
  background-color: #68e3ce;
  color: white;
  outline: none;
  border: 0;
  border-radius: 5px;
}
.message .reminder {
  margin-top: 4PX;
  padding: 8Px 0 0;
  border-top: 1px solid #d5d8d5;
  font-size: 10px;
}
