.patient-detail .pain-detail {
  color: #999999;
  font-size: 15Px;
}
.patient-detail .pic {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.patient-detail .pic > img {
  width: 45%;
  height: 45%;
  margin: 2.5% 0 0 3.3%;
  background-color: #999999;
}
.patient-detail .tag {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.patient-detail .tag .pain {
  display: block;
  margin: 4px;
  font-size: 12px;
  padding: 4px 9px;
  border: 1Px solid #999999;
  border-radius: 5Px;
}
.patient-detail .tag .active {
  border-color: #68e3ce;
  color: #68e3ce;
}
.patient-detail .button {
  width: 80%;
  margin: 0 auto;
  text-align: center;
  background-color: #68e3ce;
  color: white;
  height: 44Px;
  line-height: 44Px;
  border-radius: 22Px;
  font-size: 16Px;
}
